html, body, #root, .App, #training, .slide-content, .imageblock, .imageblock > .content  {
  height: 100%;
}


body {
  background-color: #222;
}

h1 {
  color: lightgray;
    font-size: 1.22em;
    text-align: center;
}

h4 {
  font-size: 1em;
  color: lightgray;
  display: block;
  padding: 0.4em;
  margin: 0.3em 0 0.3em 0;
  text-align: center;
}

.menu {
    max-width: 40%;
    margin: 20px auto;
}

.menu > button {
    width: 100%;
    margin-top: 2em;
}

.imageblock > .content > img {
  max-height: 100%;
  max-width: 100%;
  display: block;
  margin: 0 auto;
}


#training-panel {
  right: 0;
  bottom: 0;
  position: absolute;

}

#training-panel > button {
  background-color: darkgray;
  min-height: 4em;
  min-width: 6em;
  margin-left: 0.1em;
}


